
@use 'variables' as v;
html{
    width: 100%;
    height: 100%;
    font-size: 62.5%;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

*, 
*:before,
*:after{
    box-sizing: inherit;
}

body{
    font-size:  1.6rem;
    margin: 0;
    &.fijar-body{
        overflow: hidden;
    }
    &.body-scroll{
        padding-top: 0.1rem ;
    }
}
p{
    font-size: 2rem;
    color: v.$negro;
}
.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

a{
    text-decoration: none;
}

h1,h2,h3{
    margin: 0 0 5rem 0;
}
h1{
    font-size: 4rem;
}
h2{
    font-size: 3.6rem;
}
h3{
    font-size: 3rem;
}
img{
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}