html {
  width: 100%;
  height: 100%;
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  margin: 0;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 0.1rem;
}

p {
  font-size: 2rem;
  color: #000000;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #c7e9ff;
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
  }
}
.header .titulo h1 {
  font-size: 3rem;
  text-align: center;
  margin-bottom: 0px;
}
@media (min-width: 768px) {
  .header .titulo h1 {
    margin: 0;
    text-align: center;
  }
}
.header .titulo p {
  text-align: center;
  margin-top: 0px;
  margin-bottom: 20px;
  font-size: medium;
}
@media (min-width: 768px) {
  .header .titulo p {
    margin: 0;
    text-align: center;
  }
}
.header .contenedor-principal {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .contenedor-principal {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  color: #000000;
  font-size: 2.4rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover {
  color: #F53756;
}

.contenedor-main .presentacion {
  text-align: justify;
}
@media (min-width: 768px) {
  .contenedor-main .presentacion {
    font-size: 3rem;
  }
}
.contenedor-main h3, .contenedor-main .enlace {
  text-align: center;
  margin: 1rem;
}
@media (min-width: 768px) {
  .contenedor-main .enlace {
    font-size: 2.5rem;
  }
}

.pre-proyecto ul {
  display: flex;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 768px) {
  .pre-proyecto ul {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    justify-items: center;
  }
}
.pre-proyecto ul li {
  list-style-type: none;
}
.pre-proyecto img {
  margin-top: 2rem;
  margin-bottom: 3.5rem;
  overflow: hidden;
}
.pre-proyecto img:hover {
  transform: scale(1.2);
  opacity: 0.6;
}
.pre-proyecto .descripcion {
  text-align: center;
  margin: 0;
}

.contenedor-portafolio h1 {
  text-align: center;
}
.contenedor-portafolio img {
  overflow: hidden;
}
.contenedor-portafolio img:hover {
  transform: scale(1.1);
  opacity: 0.6;
}
.contenedor-portafolio h4 {
  margin-top: 0;
  margin-bottom: 0;
}
.contenedor-portafolio .contenedor-portafolio__listas {
  padding: 0;
}
@media (min-width: 768px) {
  .contenedor-portafolio .contenedor-portafolio__listas {
    width: 90%;
    display: flex;
    flex-direction: column;
    margin-left: 90px;
  }
}
.contenedor-portafolio .contenedor-portafolio__listas li {
  list-style-type: none;
}
.contenedor-portafolio .descripcion {
  margin: 0;
}
.contenedor-portafolio .creditovehicular, .contenedor-portafolio .huaycosos,
.contenedor-portafolio .bienesraices, .contenedor-portafolio .festivalmusical,
.contenedor-portafolio .lphuaycosos {
  margin-bottom: 2rem;
}

.contenedor-descripcion a {
  color: blue;
  font-size: 3rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.contenedor-descripcion a:hover {
  color: #752F97;
}
.contenedor-descripcion img {
  margin: 3rem 0;
}
.contenedor-descripcion .titulo {
  text-align: center;
}
.contenedor-descripcion .descripcion {
  text-align: justify;
}
@media (min-width: 768px) {
  .contenedor-descripcion .descripcion {
    margin: 1.5rem 7rem;
  }
}

.contenido-servicio h1 {
  text-align: center;
  margin-bottom: 2rem;
}

.servicios-detalles .detalles-Interfaces, .servicios-detalles .detalles-frontend-backend, .servicios-detalles .detalles-aplicacion-escritorio, .servicios-detalles .detalles-DB, .servicios-detalles .detalles-seguridad {
  list-style-type: none;
}
.servicios-detalles .detalles-Interfaces h3, .servicios-detalles .detalles-frontend-backend h3, .servicios-detalles .detalles-aplicacion-escritorio h3, .servicios-detalles .detalles-DB h3, .servicios-detalles .detalles-seguridad h3 {
  margin: 2rem 0 1rem 0;
}
.servicios-detalles .detalle {
  margin-left: 4.5rem;
}
@media (min-width: 768px) {
  .servicios-detalles .detalle {
    font-size: 2rem;
  }
}

.contenido-footer {
  background-color: rgb(155, 209, 211);
}
.contenido-footer .copyright {
  text-align: center;
}
@media (min-width: 768px) {
  .contenido-footer {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }
}

.contenedor__footer-contacto {
  text-align: center;
  padding: 0;
  margin: 0;
}
.contenedor__footer-contacto .lista-contacto {
  list-style-type: none;
  display: flex;
  justify-content: center;
}
.contenedor__footer-contacto .lista-contacto .WhatsApp, .contenedor__footer-contacto .lista-contacto .email {
  margin: 0 0 1.5rem 1rem;
}